<template>
 <div class="serviceLimit">
   <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button icon="el-icon-plus"  size="small" @click="handleAdd()" type="warning">{{$t('common.add')}}</el-button>
    </div>
    <el-form :model="limitForm" ref="limitForm" label-width="94px" label-position="left" size="small"  :rules="inputRules">
      <el-row class="label-first" :gutter="20">
        <el-col :xl="4" :lg="6">
          <el-form-item :label="$t('service.limit.url')+'：'" prop="url">
            <el-input type="text" v-model.trim="limitForm.url" clearable placeholder="eg:ip:port" maxlength="25"></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6">
          <el-button size="small" type="primary" icon="el-icon-search" @click="search()">{{$t('common.search')}}</el-button>
        </el-col>
      </el-row>
    </el-form>
    <!-- 表格部分 -->
    <el-table
      stripe
      :data="limitList"
      v-loading="loading"
      ref="multipleTable"
      style="width: 100%;">
          <!-- 服务名 -->
          <el-table-column
            align="center"
            min-width="300"
            prop="serverid"
            :label="$t('service.serviceList.serviceName')">
          </el-table-column>
          <el-table-column
            align="center"
            prop="type"
            :label="$t('service.limit.type')">
          </el-table-column>
          <el-table-column
            align="center"
            prop="value"
            :label="$t('service.limit.value')">
          </el-table-column>
          <el-table-column
            align="center"
            prop="starttime"
            :label="$t('service.limit.starttime')">
            <template slot-scope="props">
                {{props.row.starttime | timeHandle(that)}}
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            prop="endtime"
            :label="$t('service.limit.endtime')">
            <template slot-scope="props">
                {{props.row.endtime | timeHandle(that)}}
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            prop="limittime"
            :label="$t('service.limit.limittime') + '(秒)'">
          </el-table-column>
          <el-table-column
            align="center"
            prop="limitcount"
            :label="$t('service.limit.limitcount')">
          </el-table-column>
          <el-table-column
            align="center"
            prop="url"
            :label="$t('service.limit.url')">
          </el-table-column>
          <el-table-column
            width="80"
            :label="$t('common.operation')"
            fixed="right"
            align="center">
            <template slot-scope="props">
              <el-button type="text" size="mini" icon="fa fa-pencil-square-o" @click="LimitDelete(props)">{{$t('common.delete')}}</el-button>
            </template>
          </el-table-column>
    </el-table>
    <!-- 翻页部分 -->
    <div class="block">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="10"
        layout="total, prev, pager, next, jumper"
        background
        :total="totalPage">
      </el-pagination>
    </div>
    <!-- 弹出框 -->
    <el-dialog :title="'添加服务限流'" @close="cancel()" :visible.sync="popBox" width="35%" center>
      <el-form ref="popForm" :model="popForm" label-width="120px" size="small" status-icon :rules="inputRules">
        <el-row>
          <el-col :span="24">
            <el-form-item :label="$t('service.serviceList.serviceName')+'：'" prop="serverid">
              <el-input type="text" v-model.trim="popForm.serverid" placeholder="eg:MBSD_GA-1400-0101" maxlength="100" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item :label="$t('service.limit.type')+'：'" prop="type">
              <el-input type="text" v-model.trim="popForm.type" placeholder="eg:sourceType" maxlength="50" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item :label="$t('service.limit.value')+'：'" prop="value">
              <el-input type="text" v-model.trim="popForm.value" placeholder="eg:MA" maxlength="50" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item :label="$t('service.limit.starttime')+'：'" prop="starttime">
                <el-date-picker
                    v-model="popForm.starttime"
                    type="datetime"
                    placeholder="选择日期时间">
                </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item :label="$t('service.limit.endtime')+'：'" prop="endtime">
                <el-date-picker
                    v-model="popForm.endtime"
                    type="datetime"
                    placeholder="选择日期时间">
                </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item :label="$t('service.limit.limittime')+'：'" prop="limittime">
              <el-input type="text" v-model.trim="popForm.limittime" placeholder="请输入1-50位字符" maxlength="50" clearable>
                <template slot="append">seconds</template>
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item :label="$t('service.limit.limitcount')+'：'" prop="limitcount">
              <el-input type="text" v-model.trim="popForm.limitcount" placeholder="请输入1-25位字符" maxlength="25" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item :label="$t('service.limit.url')+'：'" prop="url"  placeholder="for example：10.7.94.147:8012">
              <el-input type="text" v-model.trim="popForm.url" placeholder="eg:ip:port" maxlength="25" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="popOk('popForm')" size="small">{{$t('common.ok')}}</el-button>
        <el-button @click="cancel()" size="small">{{$t('common.cancel')}}</el-button>
      </div>
    </el-dialog>
 </div>
</template>

<script type="text/ecmascript-6">
import Api from '@/constants/Api.js'
export default {
    name: 'serviceLimit',
    data() {
        return {
            that: this,
            currentPage: 1, // 当前页数
            totalPage: 0, // 总页数
            loading: false,
            limitForm: {},
            inputRules: {
                url: [
                    { required: true, message: '请输入ip端口', trigger: 'blur' },
                    // eslint-disable-next-line
                    { pattern: /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\:([0-9]|[1-9]\d{1,3}|[1-5]\d{4}|6[0-5]{2}[0-3][0-5])$/, message: '输入值必须为IP+端口' }
                ],
                serverid: [
                    { required: true, message: '请输入服务名', trigger: 'blur' }
                ],
                type: [
                    { required: true, message: '请输入限制类型', trigger: 'blur' }
                ],
                value: [
                    { required: true, message: '请输入限制值', trigger: 'blur' }
                ],
                starttime: [
                    { required: true, message: '请输入限制开始时间', trigger: 'blur' }
                ],
                endtime: [
                    { required: true, message: '请输入限制结束时间', trigger: 'blur' }
                ],
                limittime: [
                    { required: true, message: '请输入限制时间', trigger: 'blur' }
                ],
                limitcount: [
                    { required: true, message: '请输入限制次数', trigger: 'blur' }
                ]
            },
            // 全部数据
            dataBox: [],
            limitList: [],
            // 弹出框
            popBox: false,
            // 弹出框模板
            popForm: {}
        }
    },
    methods: {
        // 翻页事件
        handleCurrentChange(val) {
            this.currentPage = val
            this.limitList = this.dataBox[this.currentPage - 1]
        },
        search() {
            const query = Object.assign({}, JSON.parse(JSON.stringify(this.limitForm)))
            let {url} = query
            if (!url) {
                this.$message({
                    message: '值不能为空',
                    type: 'waring'
                })
                return false
            }
            this.loading = true
            this.$axios({
                method: 'post',
                url: Api.serviceLimitFind,
                data: query
            }).then((res) => {
                let data = JSON.parse(JSON.stringify(res.data))
                this.totalPage = data.length
                this.dataBox = this.conmmonMethods.arrayHandle(data)
                this.limitList = this.dataBox[this.currentPage - 1]
                this.loading = false
                // this.$message({
                //     message: '查询成功',
                //     type: 'success'
                // })
            }).catch((error) => {
              this.loading = false
              this.totalPage = 0
              this.dataBox = []
              this.currentPage = 1
              this.limitList = []
              this.$message.error(error.data.message || error.message || error)
            })
        },
        handleAdd() {
            this.popForm = {
                starttime: new Date(),
                endtime: new Date()
            }
            this.popBox = true
        },
        popOk(title) {
          this.$refs[title].validate((valid) => {
            if (valid) {
              let {serverid, type, value, starttime, endtime, limittime, limitcount, url} = this.popForm
              starttime = starttime.getTime()
              endtime = endtime.getTime()
              const query = Object.assign({}, {serverid, type, value, starttime, endtime, limittime, limitcount, url})
              this.loading = true
              this.$axios({
                  method: 'post',
                  url: Api.serviceLimitAdd,
                  data: query
              }).then((res) => {
                  this.$message({
                      message: '新增成功',
                      type: 'success'
                  })
                  this.limitForm.url = query.url
                  this.search()
              }).catch((error) => {
                this.loading = false
                this.$message.error(error.data.message || error.message || error)
              })
              this.popBox = false
            } else {
              this.$message({
                  showClose: true,
                  message: '带*不能为空',
                  type: 'warning'
              })
            }
          })
        },
        cancel() {
        this.$refs.popForm.resetFields()
        this.popBox = false
      },
        LimitDelete(props) {
          this.conmmonMethods.makePopBox(this, '删除').then(() => {
              let {serverid, type, value, starttime, endtime, limittime, limitcount, url} = JSON.parse(JSON.stringify(props.row))
              let query = Object.assign({}, {serverid, type, value, starttime, endtime, limittime, limitcount, url})
              this.loading = true
              this.$axios({
                method: 'post',
                url: Api.serviceLimitDelete,
                data: query
              }).then((res) => {
                this.$message({
                    message: '删除成功',
                    type: 'success'
                })
                this.limitForm.url = query.url
                this.search()
              }).catch((error) => {
                this.loading = false
                this.$message.error(error.data.message || error.message || error)
              })
          })
        }
    },
    filters: {
      timeHandle(value, that) {
        let va = that.$moment(Math.trunc(value)).format('YYYY-MM-DD HH:mm:ss')
        return va
      }
    }
}
</script>

<style scoped lang="scss">
</style>



